import crypto from "crypto"
import { getSql } from "./db"

export async function hashPassword(password: string): Promise<string> {
  return new Promise((resolve, reject) => {
    const salt = crypto.randomBytes(16).toString("hex")
    crypto.pbkdf2(password, salt, 100000, 64, "sha512", (err, derivedKey) => {
      if (err) reject(err)
      resolve(`${salt}:${derivedKey.toString("hex")}`)
    })
  })
}

export async function verifyPassword(password: string, hash: string): Promise<boolean> {
  return new Promise((resolve, reject) => {
    const [salt, key] = hash.split(":")
    crypto.pbkdf2(password, salt, 100000, 64, "sha512", (err, derivedKey) => {
      if (err) reject(err)
      resolve(key === derivedKey.toString("hex"))
    })
  })
}

export async function createUser(email: string, password: string, fullName: string, role: "client" | "nutritionist") {
  const passwordHash = await hashPassword(password)
  const sql = getSql()

  const result = await sql(
    `INSERT INTO users (email, password_hash, full_name, role) 
     VALUES ($1, $2, $3, $4) 
     RETURNING id, email, full_name, role, created_at`,
    [email, passwordHash, fullName, role],
  )

  return result[0]
}

export async function getUserByEmail(email: string) {
  const sql = getSql()
  const result = await sql(`SELECT * FROM users WHERE email = $1`, [email])
  return result[0]
}

export async function getUserById(id: string) {
  const sql = getSql()
  const result = await sql(
    `SELECT id, email, full_name, role, avatar_url, created_at, updated_at FROM users WHERE id = $1`,
    [id],
  )
  return result[0]
}
